<template>
  <div class="register">
    <p class="main-title">注册</p>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return { msg: '这是注册页面!' }
  },
   beforeRouteEnter(to, from, next) {
   	console.log("这是注册页");
   	//this.$router.push()
   	next()
    // next({path: "/register/stepFth"})
	 },
	 beforeRouteUpdate(to, from, next) {
    next()
	 },
	 beforeRouteLeave(to, from, next) {
    next()
   },
   create() {
     	console.log("parasm===> ", this.$router.params)
   }
}
</script>



